<template>
    <div id="container" style="width:600px; height:400px">
    </div>
</template>

<script>
import AMap from 'AMap';   
export default {
  mounted(){
    this.amapView();
  },
  methods: {
    amapView(){
      const map = new AMap.Map('container', {
        zoom: 11,
        center:[113.364236,23.124704],
        mapStyle: "amap://styles/light",
        viewMode:'3D'
      })
      //add some tools
      map.setDefaultCursor("crosshair");
      map.plugin(['AMap.ToolBar','AMap.Scale','AMap.MapType'],function(){
        map.addControl(new AMap.ToolBar())
        map.addControl(new AMap.Scale())
        map.addControl(new AMap.MapType({showTraffic:false,showRoad:false}))
      })
      //add some markers
      var marker = new AMap.Marker({
				icon: "https://webapi.amap.com/theme/v1.3/markers/n/mark_b.png",
        position: [113.364236,23.124704]
      });
      map.add(marker);
			var marker = new AMap.Marker({
				icon: "https://webapi.amap.com/theme/v1.3/markers/n/mark_b.png",
				position: [113.273256,23.158484]
      });
      map.add(marker);
			var marker = new AMap.Marker({
				icon: "https://webapi.amap.com/theme/v1.3/markers/n/mark_b.png",
				position: [113.319261,23.0846]
      });
      map.add(marker);
			var marker = new AMap.Marker({
				icon: "https://webapi.amap.com/theme/v1.3/markers/n/mark_b.png",
				position: [113.386209,22.937658]
      });
      map.add(marker);
      //add some real-time weather information
      map.plugin('AMap.Weather', function() {
				var weather = new AMap.Weather();
        weather.getLive('天河区', function(err, data) {
					if (!err) {
						var str = [];
            str.push('城市/区:'  + data.city);
            str.push(' 天气:' + data.weather);
            str.push(' 温度:' + data.temperature + '℃');
            str.push(' 风力:' + data.windPower + ' 级');
            str.push(' 空气湿度:' + data.humidity);
            str.push(' 发布时间:' + data.reportTime);
            var marker = new AMap.Marker({
						  map: map, 
						  position: [113.364236,23.124704]
            });
						//show the weather message
            marker.setTitle(''+str+'');
          }
        });
        weather.getLive('白云区', function(err, data) {
					if (!err) {
						var str = [];
            str.push('城市/区:'  + data.city);
            str.push(' 天气:' + data.weather);
            str.push(' 温度:' + data.temperature + '℃');
            str.push(' 风力:' + data.windPower + ' 级');
            str.push(' 空气湿度:' + data.humidity);
            str.push(' 发布时间:' + data.reportTime);
            var marker = new AMap.Marker({
						  map: map, 
						  position: [113.273256,23.158484]
            });
						//show the weather message
            marker.setTitle(''+str+'');
          }
        });
        weather.getLive('海珠区', function(err, data) {
					if (!err) {
						var str = [];
            str.push('城市/区:'  + data.city);
            str.push(' 天气:' + data.weather);
            str.push(' 温度:' + data.temperature + '℃');
            str.push(' 风力:' + data.windPower + ' 级');
            str.push(' 空气湿度:' + data.humidity);
            str.push(' 发布时间:' + data.reportTime);
            var marker = new AMap.Marker({
						  map: map, 
						  position: [113.319261,23.0846]
            });
						//show the weather message
            marker.setTitle(''+str+'');
          }
        });
        weather.getLive('番禺区', function(err, data) {
					if (!err) {
						var str = [];
            str.push('城市/区:'  + data.city);
            str.push(' 天气:' + data.weather);
            str.push(' 温度:' + data.temperature + '℃');
            str.push(' 风力:' + data.windPower + ' 级');
            str.push(' 空气湿度:' + data.humidity);
            str.push(' 发布时间:' + data.reportTime);
            var marker = new AMap.Marker({
						  map: map, 
						  position: [113.386209,22.937658]
            });
						//show the weather message
            marker.setTitle(''+str+'');
          }
        });
      });
    }
  }   
}
</script>

<style scoped>
</style>


